<div class="faq-container"><h2 class="ui header">常見問題</h2><div class="ui container"><form class="ui form"><div class="two stackable fields"><div class="field" style="max-width:150px;"><label>類別篩選</label><select class="ui dropdown" v-model="selectedCategory"><option v-for="category in categories" :value="category">{{ category }}</option></select></div><div class="field"><label>關鍵字篩選</label><div class="ui icon input"><input type="text" v-model="searchKeyword" placeholder="搜尋常見問題..."><i class="search icon"></i></div></div></div></form><table class="ui celled table"><thead><tr><th>類別</th><th>問題</th><th>回答</th><th>相關連結</th></tr></thead><tbody><tr v-for="item in filteredAndSortedFaqItems" :key="item.id"><td v-html="highlightText(item.category)"></td><td v-html="highlightText(item.question)"></td><td class="answer-cell" v-html="highlightText(parseAnswer(item.answer))"></td><td class="answer-cell"><div class="ui bulleted list" v-if="item.links &amp;&amp; parseLinks(item.links).length &gt; 0"><div class="item" v-for="link in parseLinks(item.links)" :key="link.h"><a :href="link.h" target="_blank" rel="noopener noreferrer">{{ link.t }}</a></div></div></td></tr></tbody></table></div></div>